body {
  font-family: "Poppins", sans-serif;
}

.primary-text {
  color: #00d2a7;
}

.center {
  display: flex;
  justify-content: center;
  align-items: center;
}

.App {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.light header {
  color: #000;
}

.dark {
  background-color: #181818;
}

.dark header,
.dark .bottom,
.dark main .talk-container .talk {
  color: #fff;
}

.light .bottom-background,
.light footer {
  background-color: #f4f4f4;
}

.dark .bottom-background,
.dark footer {
  background-color: #4f4e4e;
  color: #fff;
}

.bottom,
footer {
  padding: 0 20px;
}

.light footer {
  border-top: 1px solid rgba(0, 0, 0, 0.2);
}

.dark footer {
  border-top: 1px solid rgba(255, 255, 255, 0.2);
}

.dark footer a {
  color: #000;
}

.shapes .ball {
  position: absolute;
  width: 140px;
  height: 140px;
  left: -30px;
  top: 20px;
  background: radial-gradient(71.95% 71.95% at 62.8% 28.05%, #ffffff 0%, #e96d33 100%, #e96d33 100%);
  -webkit-transform: rotate(22.56deg);
          transform: rotate(22.56deg);
  border-radius: 50%;
}

.shapes .staircase {
  position: absolute;
  right: 200px;
  top: 80px;
}

.shapes .staircase div {
  position: absolute;
  width: 60px;
  height: 60px;
  border: 10px solid #9e9ec7;
  border-left: none;
  border-bottom: none;
}

.shapes .staircase div:nth-child(2) {
  left: 50px;
  top: 50px;
}

.shapes .staircase div:nth-child(3) {
  left: 100px;
  top: 100px;
  border-right: none;
}

header {
  padding: 0 10px;
  padding-top: 20px;
}

header h1 {
  font-size: 1.4rem;
  font-weight: 800;
}

header h1 .icon {
  margin-right: 5px;
  display: inline-block;
  -webkit-transform: scale(0.7);
          transform: scale(0.7);
}

main .talk-container {
  padding: 0 20px;
  padding-bottom: 50px;
}

main .talk-container .talk {
  max-width: 500px;
  color: rgba(0, 0, 0, 0.6);
  font-size: 1.1rem;
}

.bottom {
  padding-bottom: 30px;
  position: relative;
  overflow: hidden;
}

.bottom .bottom-background {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  -webkit-transform: translateY(100px);
          transform: translateY(100px);
}

.bottom .bottom-inner {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  z-index: 2;
}

.bottom .bottom-inner .course-table-container {
  width: 500px;
  padding: 10px;
  min-height: 200px;
  color: #000;
  background: #ffffff;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.25);
  border-radius: 20px;
}

.bottom .bottom-inner .course-table-container .course-table *:not(input):not(select) {
  border: none;
}

.bottom .bottom-inner .course-table-container th {
