}

.bottom .bottom-inner .calculate-btn {
  padding: 5px 40px;
  border: none;
  outline: none;
  color: #fff;
  font-weight: 800;
  background: linear-gradient(90.42deg, #2a19e6 -7.82%, #00d2a7 38.71%);
