}

footer {
  padding-bottom: 40px;
  padding-top: 40px;
  margin-top: -1rem;
  font-weight: 800;
  font-size: 1.1rem;
  text-align: center;
  position: relative;
  z-index: 2;
  flex-grow: 1;
}

footer a {
  text-decoration: none;
  color: #2a19e5;
}

